<template>
  <el-container class="heme-container">
    <!-- 头部区域 -->
    <el-header>
      <div>
        <img src="../assets/logo.png" alt="">
        <span>
          服务商后台管理系统
        <i id="did"></i>
        <el-badge :value="4" class="item add-item-badge">
          <i class="el-icon-message-solid" @click="openMessage"></i>
        </el-badge>
        </span>
      </div>
      <el-button type="success" @click="exitLoginForm" >退出系统</el-button></el-header>
    <el-container>
      <!-- 左侧菜单栏区域 -->
      <el-aside width="isCollapse ? '64px' : '200px' ">
        <el-radio-group v-model="isCollapse" style="margin-bottom: 20px;">
          <el-radio-button :label="false">展开</el-radio-button>
          <el-radio-button :label="true">折叠</el-radio-button>
        </el-radio-group>
        <el-menu unique-opened :default-active="activePath" class="el-menu-vertical-demo" @close="handleClose" :collapse="isCollapse" router>
          <!-- 一级菜单 -->
          <el-submenu :index="item.id + '' " v-for="item in menulist" :key="item.id">
            <template slot="title">
              <!-- 图标 -->
              <i :class="iconsObj[item.id]"></i>
              <!-- 文本 -->
              <span slot="title">{{item.authName}}</span>
            </template>
            <!-- 二级菜单 -->
            <el-menu-item-group>
              <el-menu-item :index=" '/' + itemtwo.path " v-for="itemtwo in item.children" :key="itemtwo.id" @click="saveNavState('/' + itemtwo.path)">
                <i class="el-icon-menu"></i>
               <span slot="title">{{itemtwo.authName}}</span>
              </el-menu-item>
            </el-menu-item-group>
            </el-submenu>
        </el-menu>
      </el-aside>
      <!-- 主体区域 -->
      <el-main>
        <!-- 子路由存在的位置 -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data () {
    return {
      // 定义一个获取时间的数据
      todaydate: '',
      // 左侧折叠状态值
      isCollapse: false,
      // 被激活的链接地址
      activePath: '',
      // 左侧菜单数据
      menulist: [],
      iconsObj: {
        125: 'iconfont icon-user',
        103: 'iconfont icon-tijikongjian',
        101: 'iconfont icon-shangpin',
        102: 'iconfont icon-danju',
        145: 'iconfont icon-baobiao'
      }
    }
  },
  // 生命周期钩子函数
  created () {
    this.getMenuList()
    // 给高亮地址赋值
    this.activePath = window.sessionStorage.getItem('activePath')
    this.todaydate = setInterval(function () {
      var date = new Date()
      var y = date.getFullYear()
      var m = (date.getMonth() + 1 + '').padStart(2, '0')
      var d = (date.getDate() + '').padStart(2, '0')
      var hh = (date.getHours() + '').padStart(2, '0')
      var mm = (date.getMinutes() + '').padStart(2, '0')

      var strdd = '星期' + '日一二三四五六'.charAt(date.getDay())
      // 拼装
      var str = y + '-' + m + '-' + d + '  ' + strdd + '  ' + hh + ': ' + mm
      document.getElementById('did').innerHTML = str
    }, 1000)
  },
  methods: {
    // 点击退出按钮
    exitLoginForm () {
      window.sessionStorage.clear()
      this.$router.push('/longin')
    },
    handleOpen (key, keyPath) {
    },
    handleClose (key, keyPath) {
    },
    // 获取所有的菜单
    async getMenuList () {
      const { data: res } = await this.$http.get('menus')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.menulist = res.data
    },
    // 保存链接的激活状态
    saveNavState (activePath) {
      window.sessionStorage.setItem('activePath', activePath)
      this.activePath = activePath
    },
    openMessage () {
      this.$router.push('/reportstwo')
    }
  }
}
</script>

<style lang="less" scoped>
  .heme-container{
    height: 100%;
  }
  .el-menu-vertical-demo:not(.el-menu--collapse) {
      width: 200px;
      min-height: 400px;
   }
  .el-header{
    background-color: #409EFF;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    font-size: 20px;
    box-shadow: 0 5 10px #ddd;
    > div {
      display: flex;
        span {
          height: 50px;
          line-height: 50px;
          padding-left: 20px;
          #did{
            font-size: 15px;
            font-style: normal;
            position: absolute;
            right: 200px;
          }
        }
        img {
          width: 50px;
        }
    }
  }
  .add-item-badge{
    position: absolute;
    top: 17px;
    right: 150px;
  }
  .el-icon-message-solid{
    position: relative;
    bottom: 12px;
    cursor: pointer;
  }
  .el-aside{
    background-color: #fff;
  }
  .el-main{
    background-color: #EAEDF1;
  }
  .iconfont{
    margin-right: 10px;
  }
  .radio-button-right{
    padding-top:10px;
  }
  .el-radio-group{
    padding-top: 20px;
  }
</style>
