<template>
  <div class="login_container">
    <Mycanvas></Mycanvas>
    <div class="login_box">
      <!-- 头像区域 -->
      <div class="avatar_box">
        <img>
      </div>
      <!-- 登录表单区 -->
      <el-form ref="loginFormRef" :rules="loginFormRules" class="login_form" :model="loginForm">
        <!-- 用户名 -->
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" prefix-icon="iconfont icon-user" placeholder="邮箱/用户名/手机号"></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="password">
          <el-input type="password" v-model="loginForm.password" prefix-icon="iconfont icon-3702mima" placeholder="密码"></el-input>
        </el-form-item>
        <!-- 按钮区域 -->
        <el-form-item class="btns">
          <el-button type="warning"  @click="theLoginForm">登录</el-button>
          <el-button type="primary" @click="resetLoginForm">重置</el-button>
        </el-form-item>
      </el-form>
      <div class="footer">
        <i>注册商标 2019-2021 版权所有</i>
        <br>
        <i>京ICP证030173号</i>
      </div>
    </div>
  </div>
</template>

<script>

import Mycanvas from './mycanvas'
export default {
  components: {
    Mycanvas
  },
  data () {
    return {
      // 这是登录表单的数据绑定对象
      loginForm: {
        username: '',
        password: ''
      },
      // 这是表单的验证规则对象
      loginFormRules: {
        // 验证用户名是否合法
        username: [
          { required: true, message: '请输入登录账号', trigger: 'blur' },
          { min: 5, max: 16, message: '长度在 5 到 16 个字符', trigger: 'blur' }
        ],
        // 验证密码是否合法
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 16, message: '没有低于 6 位数的密码', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 点击重置按钮
    resetLoginForm () {
      this.$refs.loginFormRef.resetFields()
    },
    // 表单预验证
    theLoginForm () {
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('login', this.loginForm)
        if (res.meta.status !== 200) return this.$message.error('登录失败，账号或者密码不正确')
        this.$message.success('登录成功')
        // 1。将登录成功之后的 token，保存到客户端的 sessionStorage中
        // 1.1 项目中出了登录之外的其他AIP接口，必须在登录之后才能访问
        // 1.2 token 只应在当前网站打开期间生效，所以将 token 保存在 sessionStorage中
        // 2.编程导航跳转到home
        window.sessionStorage.setItem('token', res.data.token)
        this.$router.push('/home')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.login_container{
  background-color: #2b4b6b;
  height:100%;
}

.login_box {
  width: 450px;
  height: 300px;
  background-color: rgba(255,255,255,0.4);
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  box-shadow: 0 0 10px #333;
  .avatar_box{
    margin: 0 auto;
    transform: translateY(-50%);
    height: 110px;
    width: 110px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    margin-top: 20px;
    box-shadow: 0 0 10px #ddd;
    img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background: url(../assets/icon.jpg) center;
      background-size: 100%;
      background-color: #eee;
    }
  }
}
.btns{
  display: flex;
  justify-content: flex-end;
}
.login_form{
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}
.footer {
  margin-top: 175px;
  text-align: center;
  i {
    font-size: 12px;
    color: #e5e5e5;
  }
}
</style>
